<!-- Vue component -->
<template>
<div id="big-box" class="mapz1">
    <div id="mis-search-box">
        <div id="search_pilot">
            <button v-scroll-to="{ el: value.value, onDone: onDone, offset: -150 }"> 前往 {{ value.name }} 👆</button>
        </div>
        <label class="typo__label">輸入任務名稱，或下拉選擇。再點擊上方的按鍵↑</label>
        <multiselect v-model="value" placeholder="尋找任務" label="name" track-by="desc" :options="options" :option-height="104" :custom-label="customLabel" >
            <template slot="singleLabel" slot-scope="props"><span class="option__desc"><span class="option__name">{{ props.option.desc }} @ {{ props.option.name }}</span></span></template>
            <template slot="option" slot-scope="props">
                <div class="option__desc"><span class="option__desc">{{ props.option.desc }}</span> @ <span class="option__name">{{ props.option.name }}</span></div>
            </template>
        </multiselect>
    </div>
    <div id="hot_list" class="pick-box">
        <div class="box-list">
            <div class="box-head"><h2>快速搜查</h2></div>
            <div class="box-body">
                <template v-for="pack in pepperlist.floders">
                <div class="drawer">
                    <div class="drawer-head" v-on:click="DrawClass = pack.title"><h4>{{pack.title}}</h4></div>
                    <div class="drawer-body" v-bind:class="{open_draw: DrawClass == pack.title }">
                        <template v-for="item in pack.list">
                        <div class="raid-card">
                            <div v-scroll-to="{ el: item.value, offset: -150 }" v-on:click="IsActive = item.value" class="card-body"><img :src="item.imagepath"/></div>
                        </div>                 
                        </template>
                    </div>
                </div>
                </template>
            </div>
        </div>
    </div>

    <div id="island_map">
        <div id="area_Phantagrande">
            <div class="boarder_Phantagrande islad_Phantagrande"></div>
            <div id="area_Phantagrande_west">
                <div id="Zinkenstill" v-bind:class="{ light_on: '#Zinkenstill' == IsActive }" class="islad_Phantagrande " ><span class="island_name_display">ザンクティンゼル</span></div>
                <div id="Port_Breeze_Archipelago" v-bind:class="{ light_on: '#Port_Breeze_Archipelago' == IsActive }"  ref="#Port_Breeze_Archipelago" class="islad_Phantagrande"><span class="island_name_display">ポート・ブリーズ群島</span></div>
                <div id="Valtz_Duchy" v-bind:class="{ light_on: '#Valtz_Duchy' == IsActive }" class="islad_Phantagrande" ><span class="island_name_display">バルツ公国</span></div>
                <div id="Auguste_Isles" v-bind:class="{ light_on: '#Auguste_Isles' == IsActive }" class="islad_Phantagrande" ><span class="island_name_display">アウギュステ列島</span></div>
                <div id="Lumacie_Archipelago" v-bind:class="{ light_on: '#Lumacie_Archipelago' == IsActive }" class="islad_Phantagrande"><span class="island_name_display">ルーマシー群島</span></div>
                <div id="Albion_Citadel" v-bind:class="{ light_on: '#Albion_Citadel' == IsActive }" class="islad_Phantagrande"><span class="island_name_display">城塞都市アルビオン</span></div>
            </div>
            <div class="boarder_Phantagrande islad_Phantagrande"></div>
            <div class="boarder_Phantagrande islad_Phantagrande"></div>
