<script>
import { MissionList } from "../gbf-index.js";
import { PepperList } from "../gbf-index.js";

export default {
  components: {},
  data: function() {
    return {
      DrawClass: null,
      IsActive: null,
      value: {
        name: "ダイダロイトベルト",
        value: "#Dydroit_Belt",
        desc: "ティアマト・マリスＨＬ"
      },
      options: MissionList,
      pepperlist: PepperList
    };
  },
  methods: {
    customLabel({ name, desc }) {
      return `${desc} @ ${name}`;
    },
    onDone() {
      this.IsActive = this.value.value;
    }
  }
};
</script>

